@import url('https://fonts.googleapis.com/css?family=Cormorant:wght@700');
* 
{
    box-sizing: border-box;
  }
  
  #root {
    display: flex;
    flex-direction: column;
    height: auto;
  }
  
  body {
    height: 100%;
    padding: 0;
    margin: 0;
    font-family: Cormorant;
    color: black;
    display: flex;
    flex-direction: column;
    background: url("floral-dream5.jpg")  repeat center center fixed; 
    opacity: 0.9;
  }

  .navig {
    border: solid 4px rgba(200, 0, 200, 0.1);
    border-radius: 7px;
  }

  .about {
    background-color: white;
    color: black;
    margin-top: 20px;
    padding: 20px;
    border: solid 4px rgba(200, 0, 200, 0.1);
    border-radius: 7px;
    transition: box-shadow 0.75s;
  }

  .about h4 {
    padding-left: 20px;
    padding-bottom: 5px;
  }

  .about:hover {
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.5);
  }
  
  .container {
    max-width: 800px;
    width: 100%;
    margin: 0 auto;
  }
  
  .row {
    display: flex;
    flex-wrap: wrap;
  }
  
  .col {
    display: flex;
    flex: 1 1 auto;
    padding: 15px;
    width: 100%;
  }
  
  @media screen and (min-width: 980px) {
    .col {
      width: 33.33333333333333%;
    }
  }
  
  .work-card {
    min-width: 250px;
    display: flex;
    flex-direction: column;
    padding: 10px;
    background-color: #fff;
    border: solid 4px rgba(200, 0, 200, 0.1);
    border-radius: 7px;
    will-change: transform;
    transition: box-shadow 0.75s;
  }
  
  .work-card:hover {
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.5);
  }
  
  .work-card-name {
    color: black;
    text-decoration-line: underline;
    text-decoration-color: black; 
    font-size: 22px;
    font-weight: 700;
  }
  
  .work-card-info {
    font-size: 15px;
    margin-top: 30px;
    margin-bottom: 30px;
    line-height: 1.75;
  }
  
  @media screen and (min-width: 576px) {
    .work-card-info {
      font-size: 18px;
    }
  }
  
  .img-container {
    margin-top: auto;
    padding-top: 27px;
    padding-bottom: 18px;
  }
  
  @media screen and (min-width: 576px) {
    .img-container {
      padding-bottom: 30px;
    }
  }
  
  .img-container img {
    width: 100%;
  }
  
  .img-nested-container {
    width: 100%;
    margin: 0 auto;
    max-width: 250px;
  }
  
  .dims {
    position: relative;
    width: 100%;
    height: 0;
    overflow: hidden;
  }
  
  .nested-dims {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
 